<template>
  <div id='frame'>
    <frame-header class='top'></frame-header>

    <el-row :gutter="10" class='middle'>
      <el-col :xs="0" :sm="6" :md="6" :lg="5" :xl="4" class='hidden-xs-only'>
        <frame-left class='left'></frame-left>
      </el-col>

      <el-col :xs="24" :sm="18" :md="18" :lg="19" :xl="20">
        <frame-content class='content'></frame-content>
      </el-col>
    </el-row>

    <frame-footer class='bottom hidden-sm-and-up'></frame-footer>

    <el-dialog class='dialog' :title='loginBoxTitle' :visible.sync='isLoginOpen' :close-on-click-modal='false'
      :close-on-press-escape='false' :show-close='false' :append-to-body="true" width='90%'>
      <div class='title'>
        <div class='main'>{{$t('Login.PageTitle')}}</div>
        <div class='desc'>{{$t('Login.PageDescription')}}</div>
      </div>
      <div class='loginBoxContent'>
        <comp-login ref='login'></comp-login>
        <comp-register id='register' ref='register'></comp-register>
      </div>

      <el-dropdown @command='changeLang' class='languageDropdown'>
        <div class='languageSelector'>
          语言/語言/Language
          <i class="el-icon-arrow-down el-icon--right"></i>
        </div>
        <el-dropdown-menu slot='dropdown'>
          <el-dropdown-item command='cn_zh'>简体中文</el-dropdown-item>
          <el-dropdown-item command='cn_tw'>繁體中文</el-dropdown-item>
          <el-dropdown-item command='en_us'>English</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-dialog>

    <el-dialog :visible.sync='badBrowserDialog' :close-on-click-modal='false' :close-on-press-escape='false'
      :show-close='false' fullscreen :modal='false'>
      <BrowserOutdate></BrowserOutdate>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import * as server from '.././server.js'

export default {
  name: 'main-frame',
  components: {
    FrameHeader: () => import('./frames/FrameHeader.vue'),
    FrameLeft: () => import('./frames/FrameLeft.vue'),
    FrameContent: () => import('./frames/FrameContent.vue'),
    FrameFooter: () => import('./frames/FrameFooter.vue'),
    CompLogin: () => import('./pages/comp_login/CompLogin.vue'),
    CompRegister: () => import('./pages/comp_login/CompRegister.vue'),
    BrowserOutdate: () => import('./solutions/BrowserOutdate.vue')
  },
  data () {
    return {
      isLoginOpen: false,
      loginBoxTitle: '',
      badBrowserDialog: false
    }
  },
  methods: {
    openLogin () {
      this.loginBoxTitle = this.$t('Login.Login')
      this.isLoginOpen = true;
    },
    closeLogin () {
      this.isLoginOpen = false;
    },
    switchLogin (target) {
      if (target == 'login') {
        this.loginBoxTitle = this.$t('Login.Login');
        this.$refs.login.$el.style.display = 'block';
        this.$refs.register.$el.style.display = 'none';
      }
      else if (target == 'register') {
        this.loginBoxTitle = this.$t('Login.Register');
        this.$refs.login.$el.style.display = 'none';
        this.$refs.register.$el.style.display = 'block';
      }
    },
    changeLang (lang) {
      this.switchLang(lang)
    },
    autoLogin () {
      if (Cookies.get('loginCookie')) {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.autoLogin,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginCookie')
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data.status == 'LOGIN_SUCCESS') {
              // this.notify(this.$t('Login.LoginSuccess'), this.$t('Login.LoginWelcome') + response.data.username);
              // this.$bus.$emit('closeLogin');
              Cookies.set('loginAs', Cookies.get('loginCookie'), { expires: 1 });
            }
            else {
              Cookies.remove('loginCookie');
              Cookies.remove('loginAs');
            }
          }
        })
      }
    },
    bypassBrowser (type) {
      this.badBrowserDialog = false;
      if (type == true) {
        localStorage.setItem('browserBypass', true);
      }
    }
  },
  mounted () {
    document.getElementById('frame').style.height = window.outerHeight

    if (localStorage.getItem('browserBypass') == null) {
      localStorage.setItem('browserBypass', false);
      location.reload();
    }
    else {
      if (localStorage.getItem('browserBypass') == 'false') {
        const userAgent = navigator.userAgent;
        if (/Trident\/(\S+)/.test(userAgent)) {
          this.badBrowserDialog = true;
        }
        else {
          if (Cookies.get('loginCookie')) {
            this.autoLogin();
          }
          else {
            this.openLogin();
          }
        }
      }
    }

    this.$bus.$on('bypassBrowser', this.bypassBrowser)
    this.$bus.$on('openMenu', this.openMenu);
    this.$bus.$on('closeMenu', this.closeMenu);
    this.$bus.$on('openChat', this.openChat);
    this.$bus.$on('closeChat', this.closeChat);
    this.$bus.$on('openLogin', this.openLogin);
    this.$bus.$on('closeLogin', this.closeLogin);
    this.$bus.$on('switchLogin', this.switchLogin);
  },
  beforeDestroy () {
    this.$bus.$off('bypassBrowser')
    this.$bus.$off('openMenu');
    this.$bus.$off('closeMenu');
    this.$bus.$off('openChat');
    this.$bus.$off('closeChat');
    this.$bus.$off('openLogin');
    this.$bus.$off('closeLogin');
    this.$bus.$off('switchLogin');
  }
}
</script>

<style scoped>
.top {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}
.middle {
  margin-top: 80px;
}
.left {
  padding-top: 50px;
  padding-left: 6%;
  margin-left: 10%;
}
.bottom {
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 100;
}
#register {
  display: none;
}
.languageSelector {
  cursor: pointer;
}
.languageDropdown {
  margin-top: 10px;
  font-size: 15px;
}
.dialog ::v-deep .el-dialog {
  border-radius: 25px;
  text-align: center;
  max-width: 800px;
}
.title {
  margin-top: 20px;
  margin-bottom: 30px;
  font-size: 20px;
}
.main {
  font-size: 25px;
  margin-bottom: 10px;
}
</style>